import React from 'react'
import {Box, Stack, Typography, Button } from '@mui/material'
import  HeroBannerImg  from "../assets/images/banner.png";

const HeroBanner = () => {
  return (
    <Box sx={{mt: {lg: '200px', xs: '70px'}, ml: {sm: '50px'}
    }} position="relative" p="20px" >
      <Typography color="#391213" fontWeight="600" fontSize="26px" fontFamily="inherit">
        Fitness Club
      </Typography>
      <Typography fontWeight={700} sx={{ fontSize: {lg: '44px', xs: '40px'}
    }} mb="20px" mt="30px" fontFamily="inherit">
        Sweat, Smile <br /> and Repeat
      </Typography>
      <Typography  fontSize="22px" lineHeight="35px" mb={4} fontFamily="inherit"> 
        Check out the most effective exercise
      </Typography>
      <Button variant="contained" size="medium" color="error" href='#exercises' sx={{ backgroundColor: '#391213', padding: '10px', fontFamily: 'inherit'}} >Explore</Button>
      <Typography fontWeight={700} color="#391213" sx={{
        opacity: 0.1,
        display: { lg: 'block', xs: 'none'}
      }} fontSize="200px" fontFamily="inherit">
        Exercise
      </Typography>
      <img src={HeroBannerImg} alt="banner" className="hero-banner-img"/>
    </Box>
  )
}

export default HeroBanner