import React from 'react'
import { Stack, Typography } from "@mui/material";
// import Icon from '../assets/icons/gym.png'
import Back from '../assets/icons/back.png'
import Chest from '../assets/icons/chest.png'
import LowerArm from '../assets/icons/lower_arm.png'
import LowerLeg from '../assets/icons/lower_leg.png'
import Shoulders from '../assets/icons/shoulders.png'
import UpperArms from '../assets/icons/upper_arms.png'
import UpperLegs from '../assets/icons/upper_legs.png'
import Waist from '../assets/icons/waist.png'
import All from '../assets/icons/all.png'


const BodyPart = ({ item, setBodyPart, bodyPart }) => {
  return (
    <Stack
    type="button"
    alignItems="center"
    justifyContent="center"
    className="bodyPart-card"
    sx={
      bodyPart === item ? { 
        borderTop: '4px solid #3A1212', 
        background: '#fff', 
        borderBottomLeftRadius: '20px', 
        width: '270px', 
        height: '282px', 
        cursor: 'pointer', 
        gap: '47px' 
      } : { 
        background: '#fff', 
        borderBottomLeftRadius: '20px', 
        width: '270px', 
        height: '282px', 
        cursor: 'pointer', 
        gap: '47px' }
    }
    onClick={() => {
      setBodyPart(item);
      window.scrollTo({ top: 1800, left: 100, behavior: 'smooth' });
    }}
    
  >
    <img src={(item === 'back') ? Back : 
              (item === 'chest') ?  Chest : 
              (item === 'lower arms') ? LowerArm :
              (item === 'lower legs') ? LowerLeg : 
              (item === 'shoulders') ? Shoulders : 
              (item === 'upper arms') ? UpperArms : 
              (item === 'upper legs') ? UpperLegs :
              (item === 'waist') ? Waist :
              (item === 'all') ? All : Waist} alt="dumbbell" style={{ width: '65px', height: '65px' }} />
    <Typography fontSize="24px" fontFamily="inherit" color="#3A1212" textTransform="capitalize"> {item}</Typography>
  </Stack>
  )
}

export default BodyPart